import React, { useEffect, useState, useCallback } from "react";

import "./DonatePage.css";

import { Link } from "react-router-dom";

import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import ImageLabel from "../../components/Labels/ImageLabel/ImageLabel.js";
import Button from "../../components/Buttons/index.js";
import Modal from "../../components/Modal/Modal.js";

import VersionData from "../../components/Versions/VersionData.js";

import macos_install_help from "../../img/donatePage/macos-install-help.png";


const DonatePage = ({language}) => {
    const [downloadVersion, setDownloadVersion] = useState(null);
    const [installerType, setInstallerType] = useState(null);
    const [showDonationModal, setShowDonationModal] = useState(false);
    const [showGithubModal, setShowGithubModal] = useState(false);
    const [showMacOSHelpModal, setShowMacOSHelpModal] = useState(false);
    const [startDownloadEnabled, setStartDownloadEnabled] = useState(false);
    const [donationButtonClicked, setDonationButtonClicked] = useState(false);

    useEffect(() => {
        const checkUrl = new URL(window.location.href);
        let requestedDownloadVersion = checkUrl.searchParams.get("download");
        let requestedInstallerType = checkUrl.searchParams.get("type");
        if (
            VersionData.versionList().indexOf(requestedDownloadVersion) === -1
            ||
            ["windows", "macos"].indexOf(requestedInstallerType) === -1
            ||
            VersionData.data[requestedDownloadVersion]["os"][requestedInstallerType] === false
        ) {
            setDownloadVersion(null);
            setInstallerType(null);
            checkUrl.searchParams.delete("download");
            checkUrl.searchParams.delete("type");
            window.history.replaceState(null, null, checkUrl.href);
        }
        else {
            setDownloadVersion(requestedDownloadVersion);
            setInstallerType(requestedInstallerType);
        }
    }, []);

    const startDownload = useCallback(() => {
        setStartDownloadEnabled(true);
        setTimeout(() => {
            document.getElementById("download").click();
            if (installerType === "macos") {
                setShowMacOSHelpModal(true);
            }
            else {
                setShowGithubModal(true);
            }
        }, 2000);
    }, [installerType]);

    useEffect(() => {
        const visibilityEventListener = () => {
            if (!document.hidden && donationButtonClicked && !startDownloadEnabled) {
                startDownload();
            }
        };
        if (downloadVersion !== null) {
            document.addEventListener("visibilitychange", visibilityEventListener);
        }
        return () => {
            if (downloadVersion !== null) {
                document.removeEventListener("visibilitychange", visibilityEventListener);
            }
        };
    }, [donationButtonClicked, startDownloadEnabled, startDownload, downloadVersion]);

    const T = useCallback((en, ko) => {
        if (language === "en") {
            return en;
        }
        else {
            return ko;
        }
    }, [language]);

    let donationButtons, fileName;
    if (language === "en") {
        donationButtons = (
            <>
                <Button.LinkButton onClick={setDonationButtonClicked} href="https://paypal.me/devhotteok" target="_blank">PayPal</Button.LinkButton>
                <Button.LinkButton onClick={setDonationButtonClicked} href="https://www.buymeacoffee.com/devhotteok" target="_blank">Buy Me A Coffee</Button.LinkButton>
            </>
        );
    }
    else {
        donationButtons = (
            <>
                <Button.LinkButton onClick={setDonationButtonClicked} href="https://toon.at/donate/devhotteok" target="_blank">투네이션(Toonation)</Button.LinkButton>
                <Button.LinkButton onClick={setDonationButtonClicked} href="https://www.buymeacoffee.com/devhotteok" target="_blank">Buy Me A Coffee</Button.LinkButton>
            </>
        );
    }

    let downloadArea;
    let downloadButton;
    if (downloadVersion !== null) {
        if (VersionData.versionList().indexOf(downloadVersion) < 5) {
            fileName = `TwitchLink-${downloadVersion}.exe`;
        }
        else if (installerType === "windows") {
            fileName = `TwitchLinkSetup-${downloadVersion}.exe`;
        }
        else {
            fileName = `TwitchLinkSetup-${downloadVersion}.dmg`;
        }
        if (startDownloadEnabled) {
            downloadArea = (
                <div className="downloadInProgress">
                    <Label>{T("Thanks for using TwitchLink!", "TwitchLink를 이용해 주셔서 감사합니다.")}</Label>
                    <Label>{T("Your download will begin shortly.", "곧 다운로드가 시작됩니다.")}</Label>
                    <Button.LinkButton style={{margin: "10px auto"}} id="download" href={`https://github.com/devhotteok/TwitchLink/releases/download/${downloadVersion}/${fileName}`}>
                        {T("If the download doesn't start automatically, click here.", "다운로드가 진행되지 않는다면 이곳을 누르세요.")}
                    </Button.LinkButton>
                    <Link className="LinkButton helpButton" to={`/help?lang=${language}&type=${installerType}`} target="_blank">
                        {T("Having trouble installing?", "설치하는데 문제가 있나요?")}
                    </Link>
                </div>
            );
            downloadButton = null;
        }
        else {
            downloadArea = (
                <div className="downloadInProgress">
                    <Label>{T("Please contribute to the development of TwitchLink before downloading.", "다운로드 전 TwitchLink의 개발에 기여해 주세요.")}</Label>
                </div>
            );
            downloadButton = (
                <>
                    <Button.LinkButton className="justDownloadButton" onClick={() => setShowDonationModal(true)} style={{
                        display: "flex",
                        padding: "0.3em 0",
                        fontSize: "17pt",
                        textAlign: "center",
                        justifyContent: "center",
                        alignItems: "center",
                        gap: "0.3em"
                    }}>
                        <svg style={{width: "24px", height: "24px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 -960 960 960" fill="currentColor">
                            <path d="M480-337q-8 0-15-2.5t-13-8.5L308-492q-12-12-11.5-28t11.5-28q12-12 28.5-12.5T365-549l75 75v-286q0-17 11.5-28.5T480-800q17 0 28.5 11.5T520-760v286l75-75q12-12 28.5-11.5T652-548q11 12 11.5 28T652-492L508-348q-6 6-13 8.5t-15 2.5ZM240-160q-33 0-56.5-23.5T160-240v-80q0-17 11.5-28.5T200-360q17 0 28.5 11.5T240-320v80h480v-80q0-17 11.5-28.5T760-360q17 0 28.5 11.5T800-320v80q0 33-23.5 56.5T720-160H240Z"/>
                        </svg>
                        {T("No thanks, just start my download.", "그냥 다운로드")}
                    </Button.LinkButton>
                </>
            );
            // downloadButton = (
            //     <>
            //         <p onClick={() => setShowDonationModal(true)} style={{margin: "30px auto", width: "fit-content", color: "gray", fontSize: "12pt", fontWeight: "bold", cursor: "pointer", textDecoration: "underline"}}>{T("No thanks, just start my download.", "그냥 다운로드")}</p>
            //     </>
            // );
        }
    }

    return (
        <div className="DonatePage">
            <PageFrame>
                <Content.TitleBar>{T("Donate", "후원하기")}</Content.TitleBar>
                {downloadArea}
                <Label className="donateInfo">{T("If you like the program, become a sponsor of TwitchLink!", "프로그램이 마음에 드신다면 TwitchLink의 개발을 도와주세요!")}</Label>
                <Button.ButtonArea>
                    {donationButtons}
                    {downloadButton}
                </Button.ButtonArea>
                {
                    donationButtonClicked
                    ?
                    <Label className="thankyou">{T("Thanks For Your Help!", "후원해주셔서 감사합니다.")}</Label>
                    :
                    null
                }
                <Modal
                    showModal={showMacOSHelpModal}
                    modalTitle={T("Having Trouble?", "문제가 생겼나요?")}
                    okCallback={() => {
                        setShowMacOSHelpModal(false);
                        window.open(`/help?lang=${language}&type=${installerType}`, "_blank");
                    }}
                    okButton={T("Learn more", "문제 해결하기")}
                    cancelCallback={() => {
                        setShowMacOSHelpModal(false);
                    }}
                    cancelButton={T("Close", "닫기")}
                >
                    {T(
                        "If you encounter the following message, check out how to resolve it.",
                        "다음 오류가 발생하나요? 해결책을 확인해 보세요."
                    )}
                    <br/>
                    <ImageLabel style={{display: "block", margin: "auto", maxWidth: "100%", width: "372px"}} src={macos_install_help}/>
                </Modal>
                <Modal
                    showModal={showGithubModal}
                    modalTitle={T("Visit our GitHub!", "TwitchLink GitHub를 방문해 보세요!")}
                    okCallback={() => {
                        setShowGithubModal(false);
                        document.getElementById("githubButton").click();
                    }}
                    okButton={T("OK", "확인")}
                    cancelCallback={() => {
                        setShowGithubModal(false);
                    }}
                    cancelButton={T("Cancel", "취소")}
                >
                    {T(
                        "Visit our GitHub!",
                        "TwitchLink GitHub를 방문해 보세요!"
                    )}
                    <br/>
                    {T(
                        "Help us improve features and fix bugs.",
                        "기능 개선 및 버그 수정에 참여할 수 있습니다."
                    )}
                    <Button.LinkButton id="githubButton" style={{marginTop: "20px"}} className="githubButton" href="https://github.com/devhotteok/TwitchLink" target="_blank">
                        <svg style={{width: "32px", height: "32px"}} viewBox="0 0 24 24" stroke="currentColor" stroke-width="2" fill="none" stroke-linecap="round" stroke-linejoin="round">
                            <path d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"></path>
                        </svg>
                        GitHub
                    </Button.LinkButton>
                </Modal>
                <Modal
                    showModal={showDonationModal}
                    modalTitle={T("Just Download", "그냥 다운로드")}
                    okCallback={() => {
                        setShowDonationModal(false);
                    }}
                    okButton={T("Donate", "후원하기")}
                    cancelCallback={() => {
                        setShowDonationModal(false);
                        startDownload();
                    }}
                    cancelButton={T("Just Download", "그냥 다운로드")}
                >
                    {T(
                        "TwitchLink is an open source project.",
                        "TwitchLink는 개발자의 재능기부로 운영되는 프로젝트입니다."
                    )}
                    <br/>
                    <br/>
                    {T(
                        "Please consider donating to help us continue developing TwitchLink.",
                        "TwitchLink를 지속적으로 개발할 수 있도록 후원을 검토해보세요."
                    )}
                    <br/>
                    {T(
                        "Your support is a great help to our development.",
                        "여러분의 도움은 TwitchLink의 지속적인 개발에 큰 도움이 됩니다."
                    )}
                </Modal>
            </PageFrame>
        </div>
    );
};

export default DonatePage;